@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.App-header {
  background-color: transparent;
  color: black;
  position: fixed;
  width: 98%;
  top: 0;
  left: 0;
  z-index: 1000;
  padding: 10px 20px;
}

.App-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1800px;
  margin: 0 auto;
}

.App-logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.App-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.App-nav li {
  margin: 0 15px;
}

.App-nav a {
  color: black;
  text-decoration: none;
}

.App-nav a:hover {
  text-decoration: underline;
}

.App-hero {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #FFFFEC;
}

.image-content {
  margin-top: -100px;
  margin-bottom: -50px;
}

.App-hero h1 {
  font-size: 5em;
  line-height: 1.2;
  margin-bottom: 10px;
  color: #C6A969;
}

.App-hero p {
  font-size: 1.2em;
  line-height: 1.2;
  color: #597E52;
}

.App-hero button {
  background: #597E52;
  border: none;
  color: #F1E4C3;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1em;
  border-radius: 15px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  margin-top: 20px;
}

.App-hero button:hover {
  background: #4A6B43;
}

.App-about {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #FFFFEC;
}

.about {
  padding: 40px 20px;
  background-color: #FFFFEC;
  position: relative; 
}

.info {
  background: rgba(198, 169, 105, 0.5);
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(3px);
  border: 1px solid rgba(198, 169, 105, 0.25);
  -webkit-box-shadow: 3px 3px 10px 3px #ddd;
  -moz-box-shadow: 3px 3px 10px 3px #ddd;
  box-shadow: 3px 3px 10px 3px #ddd;
  text-align: left;
  margin: 20px auto; 
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 50px;
  padding: 50px;
  border-radius: 10px;
}

.about h2 {
  margin-bottom: 20px;
}

.about p {
  font-size: 1.1em;
  max-width: 800px;
}

.image-content {
  flex: 0 0 auto; 
  display: flex;
  align-items: center; 
}

.image-content img {
  max-width: 100%;
  height: auto;
  border-radius: 8px; 
}

.image-extra {
  position: absolute; 
  width: 400px; 
  height: auto;
}

.App-footer {
  background-color: #282c34;
  color: white;
  padding: 10px;
}
